
@font-face {
    font-family: "m5x7";
    src: url(/about-me/static/media/m5x7.1c36fac5.ttf),
         url(/about-me/static/media/m5x7.b99dd26c.woff),
         url(/about-me/static/media/m5x7.1681d9b9.woff2),
         url(/about-me/static/media/m5x7.78e46408.svg#filename);
}

@font-face {
    font-family: "m6x11";
    src: url(/about-me/static/media/m6x11.cc8dfaa3.ttf),
         url(/about-me/static/media/m6x11.06395abf.woff),
         url(/about-me/static/media/m6x11.4b67f4ca.woff2),
         url(/about-me/static/media/m6x11.ea30f28d.svg#filename);
}

body {
    background-color: black;
    position: absolute;
    height: 99%;
    width: 99%;
}

a {
    text-decoration: none;
    color: rgb(124, 255, 179);
}

.logo {
    width: 128px;
    height: 128px;
    image-rendering: pixelated;
    image-rendering: -moz-crisp-edges;
    image-rendering: crisp-edges;
}
  
.header-text {
    font-family: "m6x11";
    font-size: 48px;
    font-weight: normal;
    font-style: normal;

    color: rgb(255, 255, 255);
}

.subheader-text {
    font-family: "m5x7";
    font-size: 36px;
    font-weight: normal;
    font-style: normal;
    
    color: rgb(199, 199, 199);
}

.about-text {
    font-family: 'm5x7';
    font-size: 24px;
    font-weight: normal;
    font-style: normal;
    
    color: rgb(225, 225, 225);
}

.center-align {
    text-align: center;
}

.work-box {
    background-color: rgba(0, 0, 0, 0);

    border: 16px solid transparent;
    -webkit-border-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACIAAAAiCAQAAACQTsNJAAAAdUlEQVRIie2WsRGAMAwDnxyzhoqFGCCrZI0swAyiIBBqQYflwudCb6szfKCpd73xpxNRkFHlsVxFrorQeY7cLD3PlF74bwUkIAEJSEB+A6m2vQ5IMzGVBsAMsJt3tO5MwLKamJ0VlmvK1lMgRIbxn2Qz0QZwAOv8hkbbHFLZAAAAAElFTkSuQmCC) 7.5 repeat;
            border-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACIAAAAiCAQAAACQTsNJAAAAdUlEQVRIie2WsRGAMAwDnxyzhoqFGCCrZI0swAyiIBBqQYflwudCb6szfKCpd73xpxNRkFHlsVxFrorQeY7cLD3PlF74bwUkIAEJSEB+A6m2vQ5IMzGVBsAMsJt3tO5MwLKamJ0VlmvK1lMgRIbxn2Qz0QZwAOv8hkbbHFLZAAAAAElFTkSuQmCC) 7.5 fill repeat;
    image-rendering: pixelated;
    image-rendering: -moz-crisp-edges;
    image-rendering: crisp-edges;

    -webkit-transition: 0.3s;
    z-index: 0;
}

.work-box:hover {
    z-index: 1;
    -webkit-transition: all 300ms cubic-bezier(.17,.89,.53,1.46);
    transition:         all 300ms cubic-bezier(.17,.89,.53,1.46);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    cursor: pointer;
}

.work-img {
    border: 2px solid #000000;
    padding: 2px;
    border-radius: 5px;
}

.work-title {
    font-family: "Roboto";
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    
    text-align: center;
    
    color: rgb(0, 0, 0);
}

.work-type {
    font-family: "Roboto";
    font-size: 12px;
    font-weight: bold;
    font-style: normal;
    
    text-align: center;
    
    color: rgb(27, 27, 27);
}

.work-job {
    font-family: "Roboto";
    font-size: 12px;
    font-weight: bold;
    font-style: normal;
    
    text-align: center;
    padding: 6px;
    
    color: rgb(27, 27, 27);
}
